<template>
  <collapse-transition>
    <ul :class="classes">
      <li>
        <span
          :class="arrowClasses"
          @click="handleExpand"
        >
          <icon
            v-if="showArrow"
            type="arrow-right-b"
          >
          </icon>
          <icon
            v-if="showLoading"
            type="load-c"
            class="ivu-load-loop"
          >
          </icon>
        </span>
        <checkbox
          v-if="showCheckbox"
          :value="data.checked"
          :indeterminate="data.indeterminate"
          :disabled="data.disabled || data.disableCheckbox"
          @click.native.prevent="handleCheck"
        >
        </checkbox>
        <render
          v-if="data.render"
          :render="data.render"
          :data="data"
          :node="node"
        >
        </render>
        <render
          v-else-if="isParentRender"
          :render="parentRender"
          :data="data"
          :node="node"
        >
        </render>
        <span
          v-else
          :class="titleClasses"
          @click="handleSelect"
        >
          {{ data.title }}
        </span>
        <template v-if="data.expand">
          <tree-node
            v-for="(item, i) in children"
            :key="i"
            :data="item"
            :multiple="multiple"
            :show-checkbox="showCheckbox"
            :children-key="childrenKey"
          >
          </tree-node>
        </template>
      </li>
    </ul>
  </collapse-transition>
</template>

<script>
import stubObject from 'lodash/stubObject';
import {findComponentUpward} from 'Src/utils/assist';
import Checkbox from 'Components/checkbox/checkbox.vue';
import Icon from 'Components/icon';
import CollapseTransition from 'Components/base/collapse-transition';
import Emitter from 'Src/mixins/emitter';
import Render from './render';

const prefixCls = 'ivu-tree';

export default {
  name: 'TreeNode',

  components: {Checkbox, CollapseTransition, Icon, Render},

  mixins: [Emitter],

  props: {
    childrenKey: {
      default: 'children',
      type: String,
    },
    data: {
      default: stubObject,
      type: Object,
    },
    multiple: {
      default: false,
      type: Boolean,
    },
    showCheckbox: {
      default: false,
      type: Boolean,
    },
  },

  data() {
    return {
      prefixCls,
    };
  },

  computed: {
    arrowClasses() {
      return [
        `${prefixCls}-arrow`,
        {
          [`${prefixCls}-arrow-disabled`]: this.data.disabled,
          [`${prefixCls}-arrow-open`]: this.data.expand,
        },
      ];
    },
    children() {
      return this.data[this.childrenKey];
    },
    classes() {
      return [`${prefixCls}-children`];
    },
    isParentRender() {
      const Tree = findComponentUpward(this, 'Tree');

      return Tree && Tree.render;
    },
    node() {
      const Tree = findComponentUpward(this, 'Tree');

      if (Tree) {
        // 将所有的 node（即flatState）和当前 node 都传递
        return [Tree.flatState, Tree.flatState.find((item) => item.nodeKey === this.data.nodeKey)];
      }

      return [];
    },
    parentRender() {
      const Tree = findComponentUpward(this, 'Tree');

      if (Tree && Tree.render) {
        return Tree.render;
      }

      return null;
    },
    selectedCls() {
      return [
        {
          [`${prefixCls}-node-selected`]: this.data.selected,
        },
      ];
    },
    showArrow() {
      return (
        (this.data[this.childrenKey] && this.data[this.childrenKey].length) || ('loading' in this.data && !this.data.loading)
      );
    },
    showLoading() {
      return 'loading' in this.data && this.data.loading;
    },
    titleClasses() {
      return [
        `${prefixCls}-title`,
        {
          [`${prefixCls}-title-selected`]: this.data.selected,
        },
      ];
    },
  },

  methods: {
    handleCheck() {
      if (this.data.disabled) {
        return;
      }

      const changes = {
        checked: !this.data.checked && !this.data.indeterminate,
        nodeKey: this.data.nodeKey,
      };
      this.dispatch('Tree', 'on-check', changes);
    },
    handleExpand() {
      const item = this.data;

      if (item.disabled) {
        return;
      }

      // async loading
      if (item[this.childrenKey].length === 0) {
        const tree = findComponentUpward(this, 'Tree');

        if (tree && tree.loadData) {
          this.$set(this.data, 'loading', true);
          tree.loadData(item, (children) => {
            this.$set(this.data, 'loading', false);

            if (children.length) {
              this.$set(this.data, this.childrenKey, children);
              this.$nextTick(() => this.handleExpand());
            }
          });

          return;
        }
      }

      if (item[this.childrenKey] && item[this.childrenKey].length) {
        this.$set(this.data, 'expand', !this.data.expand);
        this.dispatch('Tree', 'toggle-expand', this.data);
      }
    },
    handleSelect() {
      if (this.data.disabled) {
        return;
      }

      this.dispatch('Tree', 'on-selected', this.data.nodeKey);
    },
  },
};
</script>
